.navigation a {
  display: block;
  position: relative;
  text-decoration: none;
  text-align: center;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 15px;
  color: white;
  font-weight: bold;
}
.navigation a:focus::after,
.navigation a:hover::after {
  opacity: 1;
}
.navigation a::after {
  content: '';
  height: 100%;
  left: 0;
  opacity: 0;
  position: absolute;
  top: 0;
  -webkit-transition: opacity 0.3s;
  transition: opacity 0.3s;
  width: 100%;
}
.navigation--inline ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height:100%;
}


